function sort(data) {
	let count = {};

	let filter = [];

	for (const msg of data) {
		if (count[msg.user.id]) {
			count[msg.user.id] = {
				msg,
				count: count[msg.user.id].count + 1,
			};
		} else {
			count[msg.user.id] = {
				msg,
				count: 1,
			};
		}
	}

	for (var key in count) {
		filter.push(count[key]);
	}

	filter.sort((a, b) => {
		return b.count - a.count;
	});

	return filter;
}

export default sort;
