import "./App.css";
import { initializeApp } from "firebase/app";
import { getDatabase, ref } from "firebase/database";
import { useList } from "react-firebase-hooks/database";
import React from "react";
import sort from "./sorter";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Pie } from "react-chartjs-2";

const firebaseConfig = {
	apiKey: "AIzaSyAVFYfxd7YuAL5r7_VLYXCbXMhrLxNRsS8",
	authDomain: "clxxiii.firebaseapp.com",
	databaseURL: "https://clxxiii-default-rtdb.firebaseio.com",
	projectId: "clxxiii",
	storageBucket: "clxxiii.appspot.com",
	messagingSenderId: "851893905416",
	appId: "1:851893905416:web:085eac5c0dcc7e4e6289bd",
	measurementId: "G-59KWZZTMZL",
};

initializeApp(firebaseConfig);

ChartJS.register(ArcElement, Tooltip, Legend);

function UserList() {
	const db = getDatabase();
	let [messages] = useList(ref(db, "based_counter"));

	messages = messages.map((x) => {
		return x.val();
	});

	let list = sort(messages);

	return list.map((element) => (
		<div className="list-card">
			<img alt="profile" src={element.msg.icon_url}></img>
			<div className="name">{element.msg.user.name}</div>
			<div className="count">{element.count}</div>
		</div>
	));
}

function Chart() {
	console.log("start error");
	const db = getDatabase();
	let [messages] = useList(ref(db, "based_counter"));

	messages = messages.map((x) => {
		return x.val();
	});

	let list = sort(messages);

	const data = {
		labels: list.map((msg) => {
			return msg.msg.user.name;
		}),
		datasets: [
			{
				data: list.map((msg) => {
					return msg.count;
				}),
				backgroundColor: [
					"rgba(255, 99, 132, 0.5)",
					"rgba(54, 162, 235, 0.5)",
					"rgba(255, 206, 86, 0.5)",
					"rgba(75, 192, 192, 0.5)",
					"rgba(153, 102, 255, 0.5)",
					"rgba(255, 159, 64, 0.5)",
				],
				borderWidth: 1,
			},
		],
		options: {
			plugins: {
				legend: {
					display: false,
				},
			},
		},
	};

	const options = {
		plugins: {
			legend: {
				display: false,
			},
		},
	};

	return <Pie data={data} options={options}></Pie>;
}

function App() {
	return (
		<div class="app">
			<div className="pie-chart">
				<Chart />
			</div>
			<div className="user-list">
				<UserList />
			</div>
		</div>
	);
}

export default App;
